<template>
<div>
  <div v-if="source">
    {{source[0].title}}
    {{source[0].id}}
    <!--<tray v-bind:genere="source[0].title" v-bind:imgsource="source[0].contents"></tray>-->
  </div>
 <div v-if="source">
<div v-for="(trays,index) in source.slice(1)"  
        :key="index">
        <div v-if="trays.contents.length">   
   id : {{trays.id}}
 title : {{trays.title}}
<tray v-bind:genere="trays.title" v-bind:imgsource="trays.contents"  v-if="index % 2 !== 0" id="down"></tray>
<tray v-bind:genere="trays.title" v-bind:imgsource="trays.contents"  v-else></tray>
        </div>
        <div v-else>{{filterList(index)}}</div>
</div>


 </div>
</div>
</template>

<script>
import tray from './tray.vue'
import axios from 'axios'


export default {
  name: 'home',
  components: {
    tray,  
  },
data:function(){
  return{
    splicer:true,
    source:null
   // src:
   // [
    //  {dataurl:'https://app.lenz.ir:64014/api/v2/audio/list/home/MW_AOD_5/40/0'},
  //    {dataurl:'https://app.lenz.ir:64014/api/v2/audio/list/home/MW_AOD_7/40/0'}
 // ],
      }
  },
              mounted () {
                
                
    axios
      .get('https://app.lenz.ir:64014/api/v2/audio/home')
      .then(response => (this.source = response.data.contents));
      


  } ,


  methods: {
    goBack() {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/')
    },
  filterList:function(i){
    //for(let i=1;i < this.source.length;i++){
      //if(!this.source[i].contents.length){
console.log(i);
if(!i==0){this.source.splice(i,1);
}
      //}
   // }
  }
  }
}
</script>

